function getImageItem({ preview, original, description }) {
  return `<li class="gallery-item">
    <a class="gallery-link" href="${original}">
      <img
        class="gallery-image"
        src="${preview}"
        alt="${description}"
        />
    </a>
  </li>`;
}

const gallery = document.querySelector('ul.gallery');
const fragment = document.createElement('div');

let galleryHTML = '';

for (const image of images) {
  galleryHTML += getImageItem(image);
}

fragment.innerHTML = galleryHTML;
gallery.append(...fragment.children);

const lightbox = new SimpleLightbox('ul.gallery a', {
  captionsData: 'alt',
  captionDelay: 250,
});
